/* 翻面4 */

.conversation4 {
  width: 320px;
  height: 420px;
  position: relative;
  perspective: 1000px;
}

.cover_4,
.back4 {
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
  transition: transform .25s ease-in-out;
  justify-content: center;
  align-items: center;
  text-align: justify;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(118, 43, 25, 0.19), 0 6px 6px rgba(92, 21, 21, 0.23);

}

.cover_4 img {
  border-radius: 10px;
}

.cover_4 {
  background-image: linear-gradient(111.4deg, rgba(238, 113, 113, 1) 1%, rgba(246, 215, 148, 1) 58%);
  transform: rotateY(0deg);
  display: flex;

}

.conversation4:hover .cover_4 {
  transform: rotateY(180deg);
}

.back4 {
  background-image: linear-gradient(111.4deg, rgba(238, 113, 113, 1) 1%, rgba(246, 215, 148, 1) 58%);
  transform: rotateY(-180deg);
  position: absolute;
  overflow-y: auto;
  padding: 10px;
}

.conversation4:hover .back4 {
  transform: rotateY(0deg);
}

/* 翻面11 */
.conversation11 {
  width: 320px;
  height: 420px;
  position: relative;
