.boxR {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 650px;
    height: 250px;
    border-radius: 10px;
    background-color: rgba(124, 169, 160, 0.337);
    top: 50%;
    left: 50%;
    margin: 5%;
    margin-top: 0%;
    color: #195d3287;
}

.centered-container_Report {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
    /* 調整高度來置中整個頁面 */
}

.return-But {
    position: sticky;
    bottom: 2rem;
    right: 2rem;
    margin-left: 92%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: #9AD2C8;
    border-radius: 50%;
    text-decoration: none;
    width: 70px;
    height: 70px;

}

.return-object {
    color: white;
    font-size: 30px;
    transition: .3s ease;
}